.container {
  max-width: 1216px;
  min-width: 375px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px; }
  @media screen and (min-width: 1216px) {
    .container {
      padding: 0; } }

.delivery {
  padding-top: 20px;
  padding-bottom: 80px; }
  .delivery__wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: column;
    gap: 32px; }
  .delivery__title {
    margin-bottom: 16px;
    text-align: center; }
  .delivery__payment-item {
    margin-bottom: 8px;
    position: relative; }
    .delivery__payment-item:last-child {
      margin-bottom: 24px; }
    .delivery__payment-item::before {
      content: "";
      display: block;
      position: absolute;
      width: 8px;
      height: 8px;
      background: #c7a17a;
      border-radius: 50%;
      top: 5px;
      left: -20px; }
  .delivery__payment-btn {
    width: 335px;
    height: 48px;
    padding: 14px 20px;
    background: #c7a17a;
    border-radius: 8px;
    text-align: center;
    color: #ffffff;
    display: block;
    margin: 0 auto; }
  .delivery__shipping {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: column;
    gap: 16px; }
  .delivery__text {
    position: relative; }
  .delivery__text:first-child::before {
    content: "";
    display: block;
    position: absolute;
    width: 24px;
    height: 24px;
    left: -40px;
    top: -5px;
    background: #c7a17a; }
  .delivery__text:nth-child(2)::before {
    content: "";
    display: block;
    position: absolute;
    width: 24px;
    height: 24px;
    left: -40px;
    top: -5px;
    background: #21AF09; }
  .delivery__text:last-child::before {
    content: "";
    display: block;
    position: absolute;
    width: 24px;
    height: 24px;
    left: -40px;
    top: -5px;
    background: #FF00FF; }

.order {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: column;
  gap: 16px; }
  .order-block {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: column;
    position: relative; }
  .order-beige {
    text-align: center;
    color: #c7a17a;
    font-family: "Jost";
