  .payment__img {
    width: 100%;
    min-width: 335px;
    max-width: 1216px;
    margin: 0 auto; }
  .payment__links-wrap {
    padding-top: 16px;
    padding-bottom: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: column;
    gap: 16px; }
  .payment__links-block {
    display: flex;
